.Navigation a {
    color: #2bbc8a;
    text-decoration: none;
}

.Navigation a:hover {
    text-decoration: underline;
}

.Navigation li {
    display: inline-block;
    margin-right: 10px;
}

.Navigation li:last-child {
    border-right: 0;
}

.Navigation ul {
    padding: 0;
    margin: 23px 0 0 0;
}



